import { Component, OnInit } from '@angular/core';
import { ConfigService } from './config.service';


export class Formdata {
  name: string;
  phone: string;
  address: string;
  // carrying: string;
  total: string;
  child: string;
  vegetarian: string;
  message: string;
  join: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'weddings';
  qa_show = false;
  data = new Formdata();
  name: string;
  phone: string;
  address: string;
  // carrying: string;
  total = '1';
  child = '0';
  vegetarian = '否';
  qa_btn = true;
  participate = true;
  message: string;
  finished = false;
  sub_load: boolean;


  constructor(private addData: ConfigService) { }
  ngOnInit() {


    // this.qa_show = true;
  }
  // fun(e) {
  //   const ccc = document.getElementById(e);
  //   ccc.scrollIntoView();
  // }
  open() {
    this.qa_show = true;
  }
  submit() {
    this.sub_load = true;
    this.qa_btn = false;
    // const data = {
    //   'name': '測試先生',
    //   'phone': '0912345678',
    //   'address': '屏縣潮州鎮',
    //   'carring': '是',
    //   'total': '3',
    //   'child': '2',
    //   'vegetarian': '是',
    //   'time': '2018/08/22 10:09:05'
    // };
    if (this.participate === false) {
      this.data.join = '否';
      this.data.name = this.name;
      this.data.phone = '';
      this.data.address = '';
      this.data.child = '';
      this.data.total = '';
      this.data.vegetarian = '';
      this.data.message = this.message;
    } else {
      this.data.name = this.name;
      this.data.address = this.address;
      this.data.child = this.child;
      this.data.total = this.total;
      this.data.vegetarian = this.vegetarian;
      this.data.phone = '"' + this.phone;
      this.data.join = '是';
      this.data.message = '';
    }


    this.addData.addTrans(this.data).subscribe(x => {
      if (x === true) {
        this.qa_btn = true;
        this.finished = true;
        this.sub_load = false;
      } else {
        alert('連線問題,請重新填寫');
      }
    });
  }
}
