import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
  })
};

@Injectable({
  providedIn: 'root'
})
export class ConfigService {

  wedding_url = 'https://script.google.com/macros/s/AKfycbxBwNOprmswPkEkKNn7pemoetnxsa4ljhcKMY7BPdnq1g_ABvey/exec';

  constructor(private http: HttpClient) {

  }

  public addTrans(e): Observable<any> {

    // console.log('e:', e);
    return this.http.post(this.wedding_url, JSON.stringify(e)).pipe(map(x => {
      return x;
    }));
  }
}
